import React from 'react';

export default () => {
  return (
    <div style={{ height: "90vh"}}>
      <h1 className="display-4">About Lyon</h1>
      <p className="lead">Simple app to manage the team members of each department of the tech consulting company. Add new team members, update existing members, and observe the department's details.</p>
      <p className="text-secondary">Version 1.0.0</p>
    </div>
  );
};



